
import { getcookie,getQueryString,delQueStr,isAL,isWeiXin,isWeiXinWork,isWtsNativeApp,setcookie,getLocal,setLocal } from '@/components/tools';
import http from '@/components/http';


var domain =['http://epf.wts9999.net','https://bx.wts999.com'];
var initParams = {
    // base: base,//域名-一级
    wxscope: "snsapi_userinfo",
    zfbcode: "auth_base",
}
var appids=['wx32c3d5eef61e408d','wx32c3d5eef61e408d']
function init(){
    let origin=location.origin;
    let env=origin==domain[1]?1:0;
    initParams.domain=domain[env];
    initParams.appid=appids[env];
    toChannel();
}

/**
 *获取用户信息
 *
 * @returns
 */
function getUserInfo (callback) {
    http.ajax({
        type: "get",
        url: "/user/info",
        success: function (result, er) {
            console.log(result);
            if(result.httpCode==10000){
                return init();
            }
            callback(result.data)
        }
    });
}

/**
     * 判断是哪个渠道登录-微信，支付宝，h5
     */
    function toChannel() {
        if (isWeiXin()) {
            //微信
            wxmplogin();
        } else {
            h5mplogin();
        }
    }
    // 拉起微信授权
    function wxmplogin() {
        sessionStorage.loginUrl=location.href;
        var wxappid = initParams.appid; //appid
        var fromurl = encodeURIComponent(initParams.domain+'/login')
        var url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + wxappid + '&redirect_uri=' + fromurl + '&response_type=code&scope=' + initParams.wxscope + '&state=' + initParams.wxscope + '#wechat_redirect';
        location.href=url;
    }

    function h5mplogin() {
        
    }

    //获取地址栏参数
    function getQueryStringByVue() {
        var obj = {};
        var name,value; 
        var str=location.href; //取得整个地址栏
        var num=str.indexOf("?") 
        str=str.substr(num+1); //取得所有参数   stringvar.substr(start [, length ]
        var arr=str.split("&"); //各个参数放到数组里
        for(var i=0;i < arr.length;i++){ 
            num=arr[i].indexOf("="); 
            if(num>0){ 
            name=arr[i].substring(0,num);
            value=arr[i].substr(num+1);
            obj[name]=value;
            } 
        }
        return obj; 
    }

    export default {
        init:init,
        getUserInfo
    }